.column-1 {
  display: flex;
  flex-direction: column;
  flex: 1;
}
.row {
  justify-content: center;
  justify-items: center;
  display: flex;
  flex-direction: row;
  flex: 1;
}
.row .column-1 {
  display: flex;
  flex-direction: column;
}
.row .column-2 {
  word-break: break-all;
  display: flex;
  flex-direction: column;
  flex: 2;
  width: 50%;
}
.row .column-5 {
  display: flex;
  flex-direction: column;
  flex: 5;
  justify-self: center;
  margin: 5px;
  text-align: center;
}
.row .text-right {
  text-align: right;
}
.row .text-left {
  text-align: left;
}
.label {
  font-weight: bolder;
}
@media all and (max-width: 768px) {
  .row {
    flex-direction: column;
  }
  .row .text-right {
    text-align: center;
  }
  .row .text-left {
    text-align: center;
  }
  .row .column-5 {
    text-align: center;
  }
  .row .column-2 {
    width: 100%;
  }
}
.repo {
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  border: 1px solid #c2c2c2;
  max-width: 600px;
  margin: 20px auto;
}
.repo .title {
  background: #f5f5f5;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
}
.repo .title h3 {
  margin: 0 auto;
  padding: 5px;
}
.repo .content {
  background: white;
  padding: 10px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
}
