.column-1 {
  display: flex;
  flex-direction: column;
  flex: 1;
}
.row {
  justify-content: center;
  justify-items: center;
  display: flex;
  flex-direction: row;
  flex: 1;
}
.row .column-1 {
  display: flex;
  flex-direction: column;
}
.row .column-2 {
  word-break: break-all;
  display: flex;
  flex-direction: column;
  flex: 2;
  width: 50%;
}
.row .column-5 {
  display: flex;
  flex-direction: column;
  flex: 5;
  justify-self: center;
  margin: 5px;
  text-align: center;
}
.row .text-right {
  text-align: right;
}
.row .text-left {
  text-align: left;
}
.label {
  font-weight: bolder;
}
@media all and (max-width: 768px) {
  .row {
    flex-direction: column;
  }
  .row .text-right {
    text-align: center;
  }
  .row .text-left {
    text-align: center;
  }
  .row .column-5 {
    text-align: center;
  }
  .row .column-2 {
    width: 100%;
  }
}
.itemsList h4 {
  margin-bottom: 0;
  padding: 0;
}
.itemsList .listcontainer {
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  list-style-type: none;
  margin: auto;
  padding: 0;
  max-width: 400px;
  margin: 5px auto 10px;
  border: 1px solid transparent;
}
.itemsList .listcontainer .listitem {
  padding: 5px 0px;
  background: #fafafa;
}
.itemsList .listcontainer .listitem:nth-of-type(2n) {
  background: #f5f5f5;
}
